.cover-container{
    height: 100vh;
    width: 100%;
    object-fit: contain;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0, 0.6);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.video{
    object-fit: cover;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: -1;
}

.cover-name{
    color: #fff;
    font-family: 'Inter', sans-serif;
    font-size: 44px;
}

.cover-parrafo{
    color: #fff;
    font-family: 'Inter', sans-serif;
    margin-top: 12px;
    font-size: 20px;

}