.navbar{
    position: fixed;
    width: 100%;
    height: 15px;
    background-color: transparent;
    z-index: 1;
    top: 0;
    left: 0;
    font-size: 20px;
    cursor: pointer;
}

.scrolling{
    background-color: #fff;
    transition: all 0.5s ease;

}

.style-navbar{
    height: 15px;
    background-color: #01BD8D;
    width: 15%;
}