*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}



.container-slider{
  width: 1260px;
  margin-left: 130px;
  margin-top: 130px;

}

.slick-list {
  margin: 0 -4px;
}
.slick-slide > div {
  margin: 0 74px;
}

.image{

  height: 390px;
  border-radius: 27px;
  cursor: pointer;
  box-shadow: #01BD8D 0px 0px 0px 3px;
  transition: all 0.5s ease-in-out;

}

.image:hover{
  filter: grayscale(1);
}



.title-slider{

  text-align: center;
  font-family: 'Poppins', sans-serif;


}

.span-title{
  font-family: 'Shizuru', cursive;
  color: #01BD8D;
  font-size: 42px;
}


